import React, { useState, useEffect } from "react";
import VisibilitySensor from "react-visibility-sensor";
import "./Typing.scss";

export default function Typing({ msg, delay, isTyping, setCursor }) {
	const [message, setMessage] = useState("");
	// –ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Ä–µ–∫—É—Ä—Å–∏–∏
	// let i = 0;

	// –õ–æ–≥–∏–∫–∞ –ø–µ—á–∞—Ç–∞–Ω–∏—è –∑–¥–µ—Å—å.
	const type = () => {
		// –†–ï–®–ï–ù–ò–ï 1
		// –†–µ–∫—É—Ä—Å–∏—è - —Ä–µ—à–µ–Ω–∏–µ —Ä–∞–±–æ—Ç–∞–µ—Ç, –Ω–æ –º–Ω–µ –Ω–µ –Ω—Ä–∞–≤–∏—Ç—Å—è
		// if (i < msg.length) {
		// 	const newMsg = msg.slice(0, i + 1);
		// 	setMessage(newMsg);
		// 	i++;
		// 	setTimeout(type, Math.floor(Math.random() * 150) + 200);
		// 	i === msg.length && setTimeout(setCursor, 2000);
		// }

		// –†–ï–®–ï–ù–ò–ï 2
		// –ü–æ–ª—å–∑—É–µ–º—Å—è –∞–≤–∏–Ω—Ö—Ä–æ–Ω–Ω–æ–π –∫—Ä–∞—Å–æ—Ç–æ–π js
		// –î–ª—è –∫–∞–∂–¥–æ–π –±—É–∫–≤—ã –≤—ã—á–∏—Å–ª—è–µ–º –∏ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤—Ä–µ–º—è –ø–æ—è–≤–ª–µ–Ω–∏—è
		let _delay = delay;

		// –≤—ã—á–∏—Å–ª—è–µ–º –≤—Ä–µ–º—è –≤ –∫–æ—Ç–æ—Ä–æ–µ –±—É–¥–µ—Ç –Ω–∞–ø–µ—á–∞—Ç–∞–Ω–æ –∫–∞–∂–¥–æ–µ —Å–ª–æ–≤–æ
		for (let index = 0; index < msg.length; index++) {
			// –ò–º–∏—Ç–∏—Ä—É–µ—Ç —Ä–∞–∑–Ω—É—é —Å–∫–æ—Ä–æ—Å—Ç—å –ø–µ—á–∞—Ç–∞–Ω–∏—è
			const randomTime = Math.floor(Math.random() * 350) + 150;
			_delay = _delay + randomTime;
			// –ø–æ—Å–ª–µ–¥–Ω—è—è –±—É–∫–≤–∞
			const end = index + 1;

			setTimeout(() => {
				setMessage(msg.slice(0, end));
				// –ö–æ–≥–¥–∞ —Å–ª–æ–≤–æ –Ω–∞–ø–µ—á–∞—Ç–∞–Ω–æ, —Ç–æ –∫—É—Ä—Å–æ—Ä —É–±–∏—Ä–∞–µ–º —Å –Ω–µ–∫–æ—Ç–æ—Ä–æ–π –∑–∞–¥–µ—Ä–∂–∫–æ–π
				msg === msg.slice(0, end) && setTimeout(() => setCursor(), 1800);
			}, _delay);
		}
	};

	// https://stackoverflow.com/questions/53179075/with-useeffect-how-can-i-skip-applying-an-effect-upon-the-initial-render
	// Initializing didMount as false
	const [didMount, setDidMount] = useState(0);

	useEffect(() => {
		setDidMount(() => {
			return didMount + 1;
		});
		if (didMount === 1) {
			for (let i = 0; i < 10000; i++) {
				window.clearInterval(i);
			}
		}
		if (didMount >= 1) {
			setCursor();
			setMessage(msg);
		}
	}, [msg]); // eslint-disable-line react-hooks/exhaustive-deps

	return (
		<h3
			className={`PrintingBlock-msg Typing ${
				isTyping ? "Typing-msg-typing" : "Typing-msg-typed"
			}`}
		>
			<VisibilitySensor
				onChange={() => {
					setTimeout(type, delay);
				}}
			/>
			{isTyping && (
				<span className="Typing-msg-typed" style={{ marginRight: "15px" }}>
					{">"}
				</span>
			)}
			{message} {isTyping && <div className="Typing-cursor"></div>}
		</h3>
	);
}
